import * as React from "react";
import gsap from "gsap";

import ExpandMoreIcon from "@mui/icons-material/ExpandMore";

import {
  Box,
  IconButton,
  Stack,
  ThemeProvider,
  Typography,
  Grid,
} from "@mui/material";

import { createTheme } from "@mui/material/styles";
import HomePosts from "./HomePosts";
import headshot from "../mimoji/pc.png";
import SocialBar from "./SocialBar";

export default function Home({ posts, ids }) {
  const [workOpen, setWorkOpen] = React.useState(false);
  const [uniOpen, setUniOpen] = React.useState(false);
  const workRef = React.useRef();
  const uniRef = React.useRef();

  const ref = React.useRef();

  React.useEffect(() => {
    gsap.fromTo(
      ref.current,
      { opacity: 0, x: -400 },
      {
        duration: 1,
        x: 0,
        opacity: 1,
      }
    );
  }, []);

  const uniMove = () => {
    if (uniOpen) {
      gsap.to(uniRef.current, { duration: 0.5, x: -300, opacity: 0 });
      setUniOpen(false);
    } else {
      setUniOpen(true);
      gsap.fromTo(
        uniRef.current,
        { x: -300, opacity: 0 },
        { duration: 0.5, x: 0, opacity: 1 }
      );
    }
  };

  const workMove = () => {
    if (workOpen) {
      gsap.to(workRef.current, { duration: 0.5, x: -300, opacity: 0 });
      setWorkOpen(false);
    } else {
      setWorkOpen(true);
      gsap.fromTo(
        workRef.current,
        { x: -300, opacity: 0 },
        { duration: 0.5, x: 0, opacity: 1 }
      );
    }
  };

  const theme = createTheme({
    typography: {
      fontFamily: "Noto Sans Mono",
      h4: {
        color: "#ec6c04",
        fontWeight: 600,
      },
      h6: {
        color: "#fffffe",
      },
      body1: {
        color: "#8d99ae",
      },
      body2: {
        color: "#8d99ae",
      },
    },
  });
  return (
    <ThemeProvider theme={theme}>
      <Grid
        container
        direction="row"
        justifyContent="center"
        alignItems="center"
      >
        <Grid
          ref={ref}
          paddingLeft={2}
          container
          direction="column"
          justifyContent="flex-start"
          alignItems="stretch"
          width="600px"
        >
          <Box
            sx={{ width: "150px", paddingBottom: "20px" }}
            component="img"
            src={headshot}
          />
          <Grid
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="flex-end"
            paddingBottom={1}
          >
            <Typography variant="h4">Jacob Alcock</Typography>
            <Typography paddingLeft="2px" variant="h6">
              (he/him)
            </Typography>
          </Grid>
          <Typography variant="h6">
            Final Year Computer Science Student
          </Typography>
          {
            // current employeer and position with drop down
          }
          <Stack direction="row" spacing={2} height="25px">
            <Typography variant="body1">
              Junior Ethical Hacker @ NWCRC
            </Typography>
            <IconButton
              onClick={() => workMove()}
              sx={{ position: "relative", right: "15px" }}
            >
              <ExpandMoreIcon sx={{ color: "#8d99ae" }} />
            </IconButton>
          </Stack>
          <Box
            ref={workRef}
            visibility={workOpen ? "visible" : "hidden"}
            position={workOpen ? "relative" : "absolute"}
            sx={{ paddingX: "20px", paddingY: "10px" }}
          >
            <Typography variant="body2">
              Alongside my studies I'm a Junior Ethical Hacker at the North West
              Cyber Resilience Centre. Where I work on a variety of projects
              including security awareness training, blog posts and penetration
              testing. This oppurtinity has allowed me to develop my skills and
              follow my passion for cyber security.
            </Typography>
          </Box>
          {
            // current education with drop down
          }
          <Stack direction="row" spacing={2} height="25px">
            <Typography variant="body1">Studying @ MMU</Typography>
            <IconButton
              onClick={() => uniMove()}
              sx={{ position: "relative", right: "15px" }}
            >
              <ExpandMoreIcon sx={{ color: "#8d99ae" }} />
            </IconButton>
          </Stack>
          <Box
            ref={uniRef}
            visibility={uniOpen ? "visible" : "hidden"}
            position={uniOpen ? "relative" : "absolute"}
            sx={{ paddingX: "20px", paddingY: "10px" }}
          >
            <Typography variant="body2">
              I'm currently in my final year at Manchester Metropolitan
              University ready to graduate in June 2023! During my time so far
              I've been able to take part in a number of projects and activities
              that have helped me develop my skills and knowledge. Up to now
              I've acheived a strong 1st in degree at 82%. When I graduate I'm
              hoping to land a job in the cyber security industry.
            </Typography>
          </Box>
          <SocialBar />
        </Grid>
        <Box paddingX={"10px"}>
          <HomePosts posts={posts} ids={ids} />
        </Box>
      </Grid>
    </ThemeProvider>
  );
}
