import * as React from "react";
import MiniPost from "./MiniPost.js";

import { Typography, Box, Grid, ThemeProvider } from "@mui/material";
import { createTheme } from "@mui/material/styles";

export default function Blog({ posts, ids }) {
  const theme = createTheme({
    typography: {
      fontFamily: "Noto Sans Mono",
      h4: {
        color: "#ec6c04",
        fontWeight: 600,
      },
      h6: {
        color: "#fffffe",
      },
      body1: {
        color: "#8d99ae",
      },
      body2: {
        color: "#8d99ae",
      },
    },
  });
  return (
    <ThemeProvider theme={theme}>
      <Box sx={{ paddingTop: "20px" }}>
        <Grid
          container
          direction="column"
          justifyContent="center"
          alignItems="center"
        >
          <Typography
            variant="h4"
            sx={{ marginBottom: "20px", marginRight: "5px" }}
          >
            Blog
          </Typography>
          {posts.map((post, index) => (
            <MiniPost
              id={ids[index]}
              img={post.post.miniIMG}
              title={post.post.miniTitle}
              text={post.post.miniDesc}
            />
          ))}
        </Grid>
      </Box>
    </ThemeProvider>
  );
}
