import * as React from "react";

import gsap from "gsap";
import { Box, Grid, Typography, Paper, Stack } from "@mui/material";
import MiniPost from "./MiniPost";

export default function HomePosts({ posts, ids }) {
  const ref = React.useRef();

  React.useEffect(() => {
    gsap.fromTo(
      ref.current,
      { opacity: 0, x: 400 },
      {
        duration: 1,
        x: 0,
        opacity: 1,
      }
    );
  }, []);
  return (
    <Box paddingTop={"10px"}>
      <Typography ref={ref} variant="h4">
        Recent Posts
      </Typography>
      <Grid
        container
        paddingTop={"20px"}
        direction="column"
        justifyContent="center"
        alignItems="center"
        width={"100%"}
      >
        {posts.map((post, index) => (
          <MiniPost
            key={index}
            id={ids[index]}
            img={post.post.miniIMG}
            title={post.post.miniTitle}
            text={post.post.miniDesc}
          />
        ))}
      </Grid>
    </Box>
  );
}
