import * as React from "react";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import Button from "@mui/material/Button";
import List from "@mui/material/List";
import Divider from "@mui/material/Divider";
import ListItem from "@mui/material/ListItem";
import ListItemButton from "@mui/material/ListItemButton";
import ListItemIcon from "@mui/material/ListItemIcon";
import ListItemText from "@mui/material/ListItemText";
import IconButton from "@mui/material/IconButton";
import MenuIcon from "@mui/icons-material/Menu";
import GitHub from "@mui/icons-material/GitHub";
import LinkedIn from "@mui/icons-material/LinkedIn";
import HomeIcon from "@mui/icons-material/Home";
import BookIcon from "@mui/icons-material/Book";
import AccountBoxIcon from "@mui/icons-material/AccountBox";
import ReplayIcon from "@mui/icons-material/Replay";
import AlternateEmailIcon from "@mui/icons-material/AlternateEmail";
import { Link } from "react-router-dom";

export default function TemporaryDrawer() {
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === "keydown" &&
      (event.key === "Tab" || event.key === "Shift")
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const list = (anchor) => (
    <Box
      width={250}
      height="100%"
      backgroundColor="#262626"
      color="#fffffe"
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        <Link to={""} style={{ textDecoration: "none", color: "#fffffe" }}>
          <ListItem key={"text"} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <HomeIcon sx={{ color: "#fffffe" }} />
              </ListItemIcon>
              <ListItemText primary={"Home"} />
            </ListItemButton>
          </ListItem>
        </Link>
        <Divider />
        <Link to={"/blog"} style={{ textDecoration: "none", color: "#fffffe" }}>
          <ListItem key={"text"} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <BookIcon sx={{ color: "#fffffe" }} />
              </ListItemIcon>
              <ListItemText primary={"Blog"} />
            </ListItemButton>
          </ListItem>
        </Link>
        <Divider />
        <Link
          to={"/about"}
          style={{ textDecoration: "none", color: "#fffffe" }}
        >
          <ListItem key={"text"} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <AccountBoxIcon sx={{ color: "#fffffe" }} />
              </ListItemIcon>
              <ListItemText primary={"About"} />
            </ListItemButton>
          </ListItem>
        </Link>
        <Divider />
        <a
          href={"https://www.linkedin.com/in/jacob-alcock-43bb16230/"}
          style={{ textDecoration: "none", color: "#fffffe" }}
        >
          <ListItem key={"text"} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <LinkedIn sx={{ color: "#fffffe" }} />
              </ListItemIcon>
              <ListItemText primary={"LinkedIn"} />
            </ListItemButton>
          </ListItem>
        </a>
        <Divider />
        <a
          href={"https://github.com/JacobDavidAlcock"}
          style={{ textDecoration: "none", color: "#fffffe" }}
        >
          <ListItem key={"text"} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <GitHub sx={{ color: "#fffffe" }} />
              </ListItemIcon>
              <ListItemText primary={"GitHub"} />
            </ListItemButton>
          </ListItem>
        </a>
        <Divider />
        <a
          href={"https://jacobdavidalcock.github.io/FilmRewind/"}
          style={{ textDecoration: "none", color: "#fffffe" }}
        >
          <ListItem key={"text"} disablePadding>
            <ListItemButton>
              <ListItemIcon>
                <ReplayIcon sx={{ color: "#fffffe" }} />
              </ListItemIcon>
              <ListItemText primary={"Film Rewind"} />
            </ListItemButton>
          </ListItem>
        </a>
        <Divider />
      </List>
    </Box>
  );

  return (
    <>
      <IconButton onClick={toggleDrawer("left", true)}>
        <MenuIcon sx={{ color: "#fffffe" }} />
      </IconButton>
      <Drawer
        anchor={"left"}
        open={state["left"]}
        onClose={toggleDrawer("left", false)}
      >
        {list("left")}
      </Drawer>
    </>
  );
}
