import * as React from "react";

import { Typography, Box, Paper, Stack } from "@mui/material";
import gsap from "gsap";

import { useLocation } from "react-router-dom";

export default function Post({ posts, ids }) {
  const ref = React.useRef();
  const location = useLocation();
  const [post, setPost] = React.useState(null);
  React.useEffect(() => {
    if (posts.length > 0 && posts != null && posts != undefined) {
      setPost(posts[ids.indexOf(location.hash.slice(1))].post);
    }
  }, []);
  React.useEffect(() => {
    gsap.fromTo(
      ref.current,
      { opacity: 0, x: -400 },
      {
        duration: 1,
        x: 0,
        opacity: 1,
      }
    );
  }, [post]);
  if (post === undefined || post === null) {
    return (
      <Box>
        <Typography
          variant="h6"
          sx={{ marginBottom: "20px", marginLeft: "20px" }}
        >
          Loading...
        </Typography>
      </Box>
    );
  }
  return (
    <Box sx={{ paddingX: "20px", paddingTop: "20px" }}>
      <Paper
        ref={ref}
        elevation={20}
        sx={{ backgroundColor: "#333333", paddingX: "20px", paddingY: "20px" }}
      >
        <Typography variant="h5">{post.title}</Typography>
        <Stack spacing={3} paddingTop={"10px"}>
          {post.body.map((item, index) =>
            item.type === "text" ? (
              <Typography key={index} variant="body1">
                {item.body}
              </Typography>
            ) : item.type === "img" ? (
              <Box
                key={index}
                sx={{ maxWidth: "400px", maxHeight: "30%" }}
                component="img"
                src={item.url}
                alt="post"
              />
            ) : item.type === "video" ? (
              <Box
                sx={{ height: "60vw" }}
                key={index}
                alt="post"
                component={"iframe"}
                src={item.url}
                frameBorder="0"
              />
            ) : (
              <></>
            )
          )}
        </Stack>
      </Paper>
    </Box>
  );
}
