import * as React from "react";

import Grid from "@mui/material/Grid";
import IconButton from "@mui/material/IconButton";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import GitHubIcon from "@mui/icons-material/GitHub";
import InstagramIcon from "@mui/icons-material/Instagram";
import ReplayIcon from "@mui/icons-material/Replay";
import AlternateEmail from "@mui/icons-material/AlternateEmail";

export default function SocialBar() {
  return (
    <Grid container direction="row" alignItems="center">
      <IconButton
        onClick={() =>
          window.open("https://www.linkedin.com/in/jacob-alcock-43bb16230/")
        }
      >
        <LinkedInIcon sx={{ color: "#fffffe" }} />
      </IconButton>
      <IconButton
        onClick={() => window.open("https://github.com/JacobDavidAlcock")}
      >
        <GitHubIcon sx={{ color: "#fffffe" }} />
      </IconButton>
      <IconButton
        onClick={() =>
          window.open("https://www.instagram.com/jacobdavidalcock/")
        }
      >
        <InstagramIcon sx={{ color: "#fffffe" }} />
      </IconButton>
      <IconButton
        onClick={() =>
          window.open("https://jacobdavidalcock.github.io/FilmRewind/")
        }
      >
        <ReplayIcon sx={{ color: "#fffffe" }} />
      </IconButton>
      <IconButton
        onClick={() => window.open("mailto:jacobdavidalcock@gmail.com")}
        sx={{ color: "#fffffe" }}
      >
        <AlternateEmail />
      </IconButton>
    </Grid>
  );
}
