import * as React from "react";

import { Typography, Box, Stack, ThemeProvider, Grid } from "@mui/material";
import { createTheme } from "@mui/material/styles";

import gsap from "gsap";

import waving from "../mimoji/waving.png";
import thumbsup from "../mimoji/thumbsup.png";
import peace from "../mimoji/peace.png";

export default function About() {
  const ref = React.useRef();
  const theme = createTheme({
    typography: {
      fontFamily: "Noto Sans Mono",
      h4: {
        color: "#ec6c04",
        fontWeight: 600,
      },
      h6: {
        color: "#fffffe",
      },
      body2: {
        color: "#8d99ae",
      },
    },
  });

  React.useEffect(() => {
    gsap.fromTo(
      ref.current,
      { opacity: 0, x: -400 },
      {
        duration: 1,
        x: 0,
        opacity: 1,
      }
    );
  }, []);
  return (
    <ThemeProvider theme={theme}>
      <Box
        ref={ref}
        sx={{ paddingX: "20px", paddingTop: "20px", maxWidth: "800px" }}
      >
        <Typography variant="h4">About</Typography>
        <Box sx={{ width: "150px" }} component="img" src={waving} />
        <Stack
          spacing={"30px"}
          paddingTop={"30px"}
          justifyContent="center"
          alignItems="flex-start"
        >
          <Typography variant="body1">
            Hi, my name is Jacob! I'm currently an undergraduate computer
            science student at Manchester Metropolitan University in my final
            year. I started programming in 2012 in an afterschool club during
            primary school where I discovered my passion for computer science
            through a raspberry pi.
          </Typography>
          <Typography variant="body1">
            Since then I've continued my studies throughout my time in education
            and in my own personal development. My current focus is on
            artificial intelligence and cyber secuirty. I hope to graduate from
            MMU in 2023 and pursue a career in computer science.
          </Typography>
          <Box sx={{ width: "150px" }} component="img" src={thumbsup} />
          <Grid
            container
            direction="row"
            justifyContent="flex-start"
            alignItems="center"
          >
            <Stack
              spacing={"0px"}
              width={"250px"}
              paddingBottom={"10px"}
              marginRight={"20px"}
            >
              <Typography variant="h6" paddingBottom={"5px"}>
                Recent Technologies
              </Typography>
              <Typography variant="body2">- Python</Typography>
              <Typography variant="body2">- Matlab</Typography>
              <Typography variant="body2">- React JS</Typography>

              <Typography variant="body2">- C#</Typography>
              <Typography variant="body2">- C++</Typography>
              <Typography variant="body2">- Java</Typography>
            </Stack>
            <Stack spacing={"2px"} width={"400px"}>
              <Typography variant="h6" paddingBottom={"5px"}>
                Recent University Units
              </Typography>
              <Typography variant="body2">- Artificial Intelligence</Typography>
              <Typography variant="body2">
                - Programming Languages: Principles and Design
              </Typography>
              <Typography variant="body2">- Advanced Programming</Typography>
              <Typography variant="body2">
                - Algorithms and Data Structures
              </Typography>
              <Typography variant="body2">
                - Computer Networks and Operating Systems
              </Typography>
              <Typography variant="body2">
                - Computer System Fundamentals
              </Typography>
            </Stack>
          </Grid>
          <Box
            sx={{ width: "150px", paddingBottom: "100px" }}
            component="img"
            src={peace}
          />
        </Stack>
      </Box>
    </ThemeProvider>
  );
}
