import * as React from "react";

import gsap from "gsap";
import { Box, Typography, Paper, Stack } from "@mui/material";
import { Link } from "react-router-dom";

export default function HomePosts({ title, img, text, id }) {
  const [elevation, setElevation] = React.useState(7);
  const paperRef = React.useRef();

  React.useEffect(() => {
    gsap.fromTo(
      paperRef.current,
      { opacity: 0, x: 400 },
      {
        duration: 1,
        x: 0,
        opacity: 1,
      }
    );
  }, []);

  return (
    <Paper
      component={Link}
      to={"/post#" + id}
      ref={paperRef}
      sx={{
        textDecoration: "none",
        width: "90%",
        paddingX: "20px",
        paddingY: "10px",
        marginBottom: "20px",
        backgroundColor: "#333333",
      }}
      onMouseOver={() => setElevation(20)}
      onMouseOut={() => setElevation(7)}
      elevation={elevation}
    >
      <Stack
        container
        direction="row"
        justifyContent="start"
        alignItems="center"
      >
        <Box
          component="img"
          sx={{ width: { xs: "16vw", sm: "20vw", md: "12vw", lg: "12vw" } }}
          height={"100%"}
          src={img}
          alt="post"
        />
        <Stack paddingLeft={"20px"}>
          <Typography variant="h6">{title}</Typography>
          <Typography variant="body2">{text}</Typography>
        </Stack>
      </Stack>
    </Paper>
  );
}
