import * as React from "react";

import Home from "./Components/Home.js";
import About from "./Components/About.js";
import Blog from "./Components/Blog.js";
import Post from "./Components/Post.js";
import Sidebar from "./Components/Sidebar.js";
import Contact from "./Components/Contact.js";

import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import { CssBaseline } from "@mui/material";
import { Route, Routes } from "react-router-dom";

import { firebaseConfig } from "./Components/FirebaseConfig";
import firebase from "firebase/compat/app";
import "firebase/compat/firestore";
firebase.initializeApp(firebaseConfig);
var firestore = firebase.firestore();

async function getPosts() {
  const snapshot = await firestore.collection("Posts").get();
  return snapshot.docs.map((doc) => doc.data());
}

async function getIDs() {
  const snapshot = await firestore.collection("Posts").get();
  return snapshot.docs.map((doc) => doc.id);
}

function App() {
  const [posts, setPosts] = React.useState([]);
  const [ids, setIDs] = React.useState([]);
  React.useEffect(() => {
    getPosts().then((data) => {
      setPosts(data);
    });
    getIDs().then((data) => {
      setIDs(data);
    });
  }, []);
  if (posts.length === 0) {
    return (
      <Box>
        <Typography
          variant="h6"
          sx={{ marginBottom: "20px", marginLeft: "20px" }}
        >
          Loading...
        </Typography>
      </Box>
    );
  }
  return (
    <>
      <CssBaseline />
      <Box sx={{ paddingTop: "10px", paddingLeft: "10px" }}>
        <Sidebar />
      </Box>
      <Routes>
        <Route path="" element={<Home posts={posts} ids={ids} />} />
        <Route path="/about" element={<About />} />
        <Route path="/blog" element={<Blog posts={posts} ids={ids} />} />
        <Route path="/post" element={<Post posts={posts} ids={ids} />} />
      </Routes>
    </>
  );
}

export default App;
